import React from "react";
import { Link } from "react-router-dom";
import {Button} from "@alfalab/core-components/button";
import './styles.css';

export const LandingInfo = () => {
    const [open, setOpen] = React.useState(false)
    return (
        <div>
            <h2 className="landing-title">ROKON -   !</h2>
            <p className="landing-text"> ,     ,    -  
             .    - ,       <b>  </b> !
            </p>

            <div>
                <Button 
                className = "button button-reg">
                    <Link className={`registration `} to="/register" onClick={()=>setOpen(!open)}>  !</Link>
                </Button>
            </div>

            <h2 className="landing-sec">   ?</h2>
            <div className="landing-steps">

            <svg className="circle1" viewBox="0 0 80 80" width="80" height="80">
                        <circle className="circle" cx="40" cy="40" r="38"/>
                    </svg>
                <p className="step1">     </p>

                    <svg className="circle2" viewBox="0 0 80 80" width="80" height="80">
                        <circle className="circle" cx="40" cy="40" r="38"/>
                    </svg>    
                <p className="step2">    </p>


                    <svg className="circle3" viewBox="0 0 80 80" width="80" height="80">
                        <circle className="circle" cx="40" cy="40" r="38"/>
                    </svg>   
                <p className="step3">       </p>

                    <svg className="circle4" viewBox="0 0 80 80" width="80" height="80">
                        <circle className="circle" cx="40" cy="40" r="38"/>
                    </svg>
                <p className="step4">   !</p>

                    <div>
                <Button 
                className = "button button-want">
                    <Link className={`wantit`} to="/mission" onClick={()=>setOpen(!open)}>  !</Link>
                </Button>
                    </div>
            </div>

            
            <div className="marginkok">
                <form></form>
            </div>

        </div>

       
    )
}